.image-item-container {
  position: relative;
  overflow: hidden;
}

.image-title {
  position: absolute;
  top: 10px; /* Adjust as needed */
  left: 10px; /* Adjust as needed */
  margin: 0;
  color: white;
  z-index: 1;
}

.art {
  transition: filter 0.3s ease;
}

.art.darken {
  filter: brightness(50%); /* Darken the image on hover */
}

.image-description {
  position: absolute;
  bottom: -100%; /* Initially hide the description */
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  color: white;
  padding: 10px;
  transition: bottom 0.2s ease;
}

.image-description.visible {
  bottom: 0; /* Display the description on hover */
}
